import { ref, watch, computed } from "vue";
import { ElButton } from "element-plus";
export default {
  props: {
    time: Number
  },
  components: {
    ElButton
  },

  setup(props, {
    emit
  }) {
    //获取time时分秒
    const timer = ref(0);
    const stop = ref(true);
    const touching = ref(false);
    watch(() => props.time, val => {
      timer.value = val;
    }, {
      immediate: true
    }); //倒计时

    const countDown = () => {
      const down = () => {
        console.log(stop.value);

        if (timer.value <= 0 || stop.value) {
          return;
        }

        timer.value -= 1000;
      };

      setInterval(down, 1000);
      return {
        handleStop: () => {
          stop.value = true;
        },
        handleStart: () => {
          console.log(stop.value);
          stop.value = false;
        }
      };
    };

    const format = num => {
      return num < 10 ? "0" + num : num;
    };

    const formatTime = computed(() => {
      let time = timer.value; //秒转时分秒, 两位数

      let h = format(Math.floor(time / 1000 / 3600));
      let m = format(Math.floor((time / 1000 - h * 3600) / 60));
      let s = format((time / 1000 - h * 3600 - m * 60).toFixed(0)); //时间戳获取毫秒

      let mmm = time % 1000; // let mmm = (time - h * 3600 * 1000 - m * 60 * 1000 - s * 1000).toFixed(0);

      return {
        s,
        m,
        h,
        mmm
      };
    });
    const {
      handleStop,
      handleStart
    } = countDown();

    const listenMove = (e, type) => {
      touching.value = type;
      handleStop(); //获取currentTarget

      const el = e.currentTarget; //去除transition

      el.style.transition = "none";
      const startY = e.clientY || e.touches[0].clientY;
      const translateY = parseInt(el.style.transform.split("(")[1]);
      console.log(translateY);
      const offset = startY - translateY;
      let newTranslateY = translateY; //监听el的mousemove事件

      function mouseMoveHandle(e) {
        e.stopPropagation();
        const currentY = e.clientY || e.touches[0].clientY;
        console.log(currentY);
        newTranslateY = currentY - offset;
        console.log(newTranslateY);
        el.style.transform = `translateY(${newTranslateY}px)`; // calcCurrentTime(newTranslateY);
      }

      function calcCurrentTime(newTranslateY) {
        //通过偏移量计算当前时间
        const idx = -Math.floor((newTranslateY + 50) / 100);
        let h = formatTime.value.h;
        let m = formatTime.value.m;
        let s = formatTime.value.s;

        switch (type) {
          case "h":
            h = idx > 23 ? 23 : idx < 0 ? 0 : idx;
            break;

          case "m":
            m = idx > 59 ? 59 : idx < 0 ? 0 : idx;
            break;

          case "s":
            s = idx > 59 ? 59 : idx < 0 ? 0 : idx;
            break;
        }

        emit("changeTime", {
          h,
          m,
          s
        });
      }

      el.addEventListener("touchmove", mouseMoveHandle);
      el.addEventListener("touchend", () => {
        el.removeEventListener("mousemove", mouseMoveHandle);
        calcCurrentTime(newTranslateY); //添加transition
        // start();

        el.style.transition = "all .2s";
        touching.value = false;
      });
    };

    return {
      timer,
      stop,
      listenMove,
      handleStart,
      handleStop,
      formatTime,
      format,
      touching
    };
  }

};