// import { ElTimePicker } from "element-plus";
import countDown from "./components/countDown.vue";
import { ref } from "vue";
import * as dayjs from "dayjs";
export default {
  components: {
    countDown
  },

  setup() {
    const show = ref(true);
    const time = ref(0);
    const countDownRef = ref(null);
    const selectTime = ref(+dayjs().startOf("day"));

    function select(val) {
      const now = +dayjs().startOf("day");
      const seletDate = +new Date(val);

      if (seletDate - now < 0) {
        alert("时间不能小于当前时间");
        return;
      }

      selectTime.value = seletDate;
      time.value = seletDate - now;
    }

    function changeTime(cTime) {
      let selTime = dayjs(selectTime.value); //设置selTime的时分秒

      selTime = selTime.set("hour", cTime.h).set("minute", cTime.m).set("second", cTime.s);
      console.log(cTime.h);
      console.log(selTime.$d);
      select(+selTime); // time.value = countDownRef.value;
    }

    function exitFullscreen() {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen();
      }
    }

    function launchFullscreen(element = countDownRef.value.$el) {
      console.log(element);

      if (element.requestFullscreen) {
        element.requestFullscreen();
      } else if (element.mozRequestFullScreen) {
        element.mozRequestFullScreen();
      } else if (element.msRequestFullscreen) {
        element.msRequestFullscreen();
      } else if (element.webkitRequestFullscreen) {
        element.webkitRequestFullScreen();
      }
    }

    return {
      time,
      select,
      show,
      selectTime,
      exitFullscreen,
      launchFullscreen,
      countDownRef,
      changeTime
    };
  }

};